.input_text_bottom input[type=checkbox] + label {
  display: block;
  margin: 0.2em;
  cursor: pointer;
  padding: 0.2em;
}

.input_text_bottom input[type=checkbox] {
  display: none;
}

.input_text_bottom input[type=checkbox] + label:before {
  content: "✔";
  content: "✔";
  border: 0.1em solid #B8B8B8;
  border-radius: 4px;
  display: inline-block;
  width: 11px;
  height: 11px;
  padding-left: 0.2em;
  padding-bottom: 0.3em;
  margin-right: 0.2em;
  vertical-align: bottom;
  color: transparent;
  transition: 0.2s;
  margin-right: 8px;
  margin-bottom: 2px;
}

.input_text_bottom input[type=checkbox] + label:active:before {
  transform: scale(0);
}

.input_text_bottom input[type=checkbox]:checked + label:before {
  background-color: #419b95;
  border-color: #419b95;
}

.input_text_bottom input[type=checkbox]:disabled + label:before {
  transform: scale(1);
  border-color: #aaa;
}

.input_text_bottom input[type=checkbox]:checked:disabled + label:before {
  transform: scale(1);
  background-color: #bfb;
  border-color: #bfb;
}

